import React from "react";
import { connect } from "react-redux";
import Modal from "react-modal";
import { bindActionCreators } from "redux";
import * as lendingCriteriaActions from "../../actions/lendingCriteriaActions";
import * as investingCriteriaActions from "../../actions/investingCriteriaActions";
import InvestingCriteriaForm from "./InvestingCriteriaForm";

class InvestingCriteriaAddContainer extends React.Component {
  componentDidMount() {
    this.props.actions.getFundTypes();
    this.props.actions.getReturnTypes();
    this.props.actions.getInvestmentTypes();
    this.props.actions.getConstructionTypes();
    this.props.lendingCriteriaActions.getPropertyTypes();
    this.props.lendingCriteriaActions.getStateList();
  }

  render() {
    return (
      <div>
        {this.props.investingCriteria.fundTypesList.length &&
        this.props.investingCriteria.returnTypesList.length &&
        this.props.investingCriteria.investmentTypesList.length &&
        this.props.investingCriteria.constructionTypesList.length &&
        this.props.lendingCriteria.propTypesList.length &&
        this.props.lendingCriteria.stateList.length &&
        !this.props.investingCriteria.loading ? (
          <InvestingCriteriaForm
            message={this.props.investingCriteria.toast.message}
            fundTypesList={this.props.investingCriteria.fundTypesList}
            returnTypesList={this.props.investingCriteria.returnTypesList}
            investmentTypesList={
              this.props.investingCriteria.investmentTypesList
            }
            propTypesList={this.props.lendingCriteria.propTypesList}
            constructionTypesList={
              this.props.investingCriteria.constructionTypesList
            }
            statesList={this.props.lendingCriteria.stateList}
            submit={this.props.actions.addInvestingCriteria}
          />
        ) : (
          <Modal
            className="loading-modal"
            isOpen={true}
            ariaHideApp={false}
            contentLabel="Modal"
            shouldCloseOnOverlayClick={false}
          >
            <div className="centered-spinner">
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading-spinner"
              />
            </div>
          </Modal>
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    investingCriteria: state.investingCriteria,
    lendingCriteria: state.lendingCriteria,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(investingCriteriaActions, dispatch),
    lendingCriteriaActions: bindActionCreators(
      lendingCriteriaActions,
      dispatch
    ),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(InvestingCriteriaAddContainer);
